<template>
  <div>
    <div class="effects" :class="{expanded}">
      <a @click="effectsPressed" href="#"><i class="fa fa-bars fa-2x"></i></a>
      <div v-for="effect in effects" class="effect" :class="effectClasses(effect)" @click="effectSelected(effect)"><span>{{effect.substring(0, 1).toUpperCase()}}</span></div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'effects',
  data () {
    return {
      expanded: false,
      effects: ['tonic', 'fifth', 'glissando', 'strings'],
      activeEffect: 'glissando'
    }
  },
  computed: {
    ...mapGetters(['effect'])
  },
  methods: {
    ...mapActions(['setEffect']),
    effectClasses (effect) {
      var classes = [effect]
      if (this.effect === effect) {
        classes.push('current')
      }
      return classes
    },
    effectsPressed () {
      this.expanded = !this.expanded
    },
    effectSelected (effect) {
      if (this.expanded) {
        this.setEffect(effect)
      } else {
        this.expanded = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

.effects {
  display: flex;
  flex-direction: row;
  align-items: center;
  a {
    color: white;
    margin-right: 10px;
  }
  i {
    transition: transform 0.7s;
  }
  &.expanded {
    .effect {
      display: flex;
    }
    i {
      transform: rotate(-90deg);
    }
  }
  .effect {
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    margin-right: 10px;
    border-radius: 25px;
    border: solid 2px transparent;
    color: white;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    &.current {
      border: solid 2px white;
      font-size: 22px;
      text-shadow: 0px 0px 3px black;
    }
    &.tonic {
      background-color: @color-tonic;
    }
    &.fifth {
      background-color: @color-fifth;
    }
    &.glissando {
      background-color: @color-blue;
    }
    &.strings {
      background-color: #ff9911;
    }
  }
  .effect.current {
    display: flex;
  }
}
</style>



